.dashboard__content {
    width: 95%;
    height: 98%;
    margin: auto;
    background-color: $fondo-panel;
}

.dashboard__content-heard {
    display: flex;
    width: 100%;
}

.dashboard__content-heard-card {
    background-color: $fondo-panel;
    text-align: center;
    margin: auto;
    padding: 20px;
    background-color: $fondo-panel;
}

.dashboard__content-heard-card-icon {
    color: #8400ff;
}

.dashboard__content-heard-card-number {
    font-size: 28px;
    color: $texto-principal;
    font-weight: bold;
}

.dashboard__content-heard-card-text {
    color: $texto-principal;
}

.dashboard__content-report {
    display: flex;
    height: 30%;
}

.dashboard__content-repot-left {
    width: 60%;
    height: auto;
    padding: 0px;
}

.dashboard__content-repot-right {
    width: 40%;
    height: auto;
    padding: 0px 60px;
}

.dashboard__content-resume {
    padding: 5px;
    margin: auto;
    color: $texto-principal;
    display: flex;
    height: 1%;
}

.dashboard__content-resume-left {
    text-align: center;
    margin: auto;
}

.dashboard__content-resume-right {
    text-align: center;
    margin: auto;
}

//MODAL EVENTOS
//||--||MODAL EVENTOS||--||
.modal-calendarmodal-ysm {
    background: $fondo-panel;
    border-radius: 5px;
    color: $texto-principal;
    display: inline;
    max-height: 50%;
    max-width: 600px;
    outline: none;
}

//        ||EVENTOS PRINCIPAL||
//HEADER
.header-calendarmodal-ysm {
    height: 15%;
    width: 100%;
    background-color: $iconos;
}

.titulo-calendarmodal-ysm {
    width: 100%;
    height: 100%;
    color: black;
    font-size: xx-large;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

//SUBHEADER
.subheader-calendarmodal-ysm {
    height: 10%;
    width: 100%;
    opacity: 0.9;
}

.fechainicio-subheader-ysm {
    height: 100%;
    width: 50%;
    float: left;
    background-color: $iconos;
    color: black;
    opacity: 0.9;
    font-size: x-large;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.fechafin-subheader-ysm {
    height: 100%;
    width: 50%;
    float: right;
    background-color: $iconos;
    color: black;
    opacity: 0.9;
    font-size: x-large;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

//BODY
.body-calendarmodal-ysm {
    height: 65%;
    width: 100%;
    background-color: $iconos;
    color: black;
    opacity: 0.7;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

//FOOTER
.footer-calendarmodal-ysm {
    height: 10%;
    width: 100%;
    background-color: $iconos;
}

.link-footer-ysm {
    height: 100%;
    width: 100%;
    color: rgb(0, 0, 0);
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

//ESTILOS DE ICONOS
.icono-dashboard-ysm {
    color: $iconos;
    cursor: pointer;
}

